<template>
  <div class="field mt-5 vertical-align">
    <div class="control">
      <label class="radio">
        <input
          type="radio"
          value="public"
          v-model="status"
          @change="clickRadioButton"
        />
        公開
      </label>
      <label class="radio">
        <input
          type="radio"
          value="private"
          v-model="status"
          @change="clickRadioButton"
        />
        非公開
      </label>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      status: "public",
    };
  },
  methods: {
    clickRadioButton() {
      this.$emit("changeRadio", this.status);
    },
  },
};
</script>

<style scoped>
.vertical-align input {
  vertical-align: middle;
}
</style>